
  var Bmob = require('../../libs/bomb.js');
  Bmob.initialize("59d4343f153eb8b37eae01aa766216d6", "98c5871b09d16628b04d51c03fb06bcc");

  export const getSetting= callback=>{
    const query = Bmob.Query("setting");
    query.find().then(res => {
        callback(res);
    });
  };

  export const getBlogList= callback=>{
    const query = Bmob.Query("blogs");
    query.find().then(res => {
        callback(res);
    });
  };

  export const getEventList= callback=>{
    const query = Bmob.Query("events");
    query.find().then(res => {
        callback(res);
    });
  };

  export const getPhotoList= callback=>{
    const query = Bmob.Query("photos");
    query.find().then(res => {
        callback(res);
    });
  };

  export const getBlogListByPage= (page,pagesize,callback)=>{
    const query = Bmob.Query("blogs");
    query.limit(pagesize);
    query.skip((page-1)*pagesize);
    query.find().then(res => {
        callback(res);
    });
  };

  export const getEventListByPage= (page,pagesize,callback)=>{
    const query = Bmob.Query("events");
    query.limit(pagesize);
    query.skip((page-1)*pagesize);
    query.find().then(res => {
        callback(res);
    });
  };
  export const newblog=(title,content,author,thumb,callback)=>{
    const query = Bmob.Query('blogs');
    query.set("title",title)
    query.set("content",content)
    query.set("author",author)
    query.set("thumb",thumb)
    query.save().then(res => {
        callback(res)
    }).catch(err => {
        console.log(err)
    })
  }

  export const updateblog=(blog,callback)=>{
    const query = Bmob.Query('blogs');
    query.set("id",blog.objectId)
    query.set("title",blog.title)
    query.set("content",blog.content)
    query.set("author",blog.author)
    query.set("thumb",blog.thumb)
    query.save().then(res => {
        callback(res)
    }).catch(err => {
        console.log(err)
    })
  }

  export const getPageList= callback=>{
    const query = Bmob.Query("pages");
    query.find().then(res => {
        callback(res);
    });
  };

  export const newpage=(title,content,author,thumb,callback)=>{
    const query = Bmob.Query('pages');
    query.set("title",title)
    query.set("content",content)
    query.set("author",author)
    query.set("thumb",thumb)
    query.save().then(res => {
        callback(res)
    }).catch(err => {
        console.log(err)
    })
  }

  export const updatepages=(blog,callback)=>{
    const query = Bmob.Query('pages');
    query.set("id",blog.objectId)
    query.set("title",blog.title)
    query.set("content",blog.content)
    query.set("author",blog.author)
    query.set("thumb",blog.thumb)
    query.save().then(res => {
        callback(res)
    }).catch(err => {
        console.log(err)
    })
  }

  export const updatesetting=(setting,callback)=>{
    const query = Bmob.Query('setting');
    query.set("id",setting.objectId)
    query.set("name",setting.name)
    query.set("desc",setting.desc)
    query.set("avatar",setting.avatar)
    query.save().then(res => {
        callback(res)
    }).catch(err => {
        console.log(err)
    })
  }

  export const delblog=(id,callback)=>{
    const query = Bmob.Query("blogs");
    query.destroy(id).then(res => {
        callback(res);
    });
  }

  export const login= (username,password,callback)=>{
    Bmob.User.login(username,password).then(res => {
        callback(res)
      }).catch(err => {
       console.log(err)
     });
  };

  export const getBlogDetail=(id,callback)=>{
    const query = Bmob.Query("blogs");
    query.equalTo('objectId', '==', id)
    query.find().then(res => {
        callback(res);
    });
  }

  export const getUserList= callback=>{
    const query = Bmob.Query("_User");
    query.find().then(res => {
        callback(res);
    });
  };
  
  export const deluser=(id,callback)=>{
    const query = Bmob.Query("_User");
    query.destroy(id).then(res => {
        callback(res);
    });
  }


