<template>
  <section>
<footer class="footer">  
      <div class="container content has-text-centered">        
        <h5 class="is-5">{{$t('m.social_media')}}</h5>
        <div class="columns is-centered">
          <div class="column is-7 is-12-mobile">
            <div class="columns is-gapless">
              <div class="column" :key="nav.li" v-for="nav in navs">      
                  <a :href="nav.url" target="_blank" @click="open" :data-pic="nav.pic" class="navbar-item">
                    <span class="iconfont" :class="nav.class" :title="nav.li"> {{nav.li}}</span>
                  </a>
              </div>
            </div>
          </div>
        </div>
    <hr>
  <div class="content has-text-centered">
    <p>
      &copy; 2019 <router-link class="url" to="/">migao.io</router-link>
    </p>
  </div>
    </div>
</footer>
  </section>
</template>
<style>
  .url{
    text-transform: uppercase;
  }
</style>
<script>
export default {
  name: 'Footer',
  data(){
      return {
        navs:[{
            li: this.$root.$t('m.weibo'),
            url: 'http://weibo.com/michaelinsh',
            class: 'icon-weibo'
        },
        {
            li: 'Strava',
            url: 'https://www.strava.com/athletes/6133205',
            class: 'icon-strava'
        },
        {
            li: 'Instagram',
            url: 'http://www.instagram.com/walkingp',
            class: 'icon-instagram'
        },
        {
            li: this.$root.$t('m.wechat'),
            pic: '/assets/wechat.png',
            class: 'icon-wechat'
        },
        {
            li: this.$root.$t('m.miniProgram'),
            pic: '/assets/miniP.jpg', 
            class: 'icon-xcx'
        }]
      }
  },
  methods: {
    open(e){
      var pic=e.currentTarget.dataset.pic;
      console.log(pic)
      if(pic){
        this.$zoom(pic);
      }
    }
  },
}
</script>


