<template>
<section class="is-header">
    <nav class="navbar" role="navigation" aria-label="main navigation">
    <div class="container">
        <div class="navbar-brand">
            <router-link class="navbar-item logo" to="/">
                <img src="../assets/migao.png" alt="migao.io" />
            </router-link>

            <a role="button" class="navbar-burger burger" @click="showNav = !showNav" :class="{ 'is-active': showNav }" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            </a>
        </div>

        <div id="navbarBasicExample" class="navbar-menu" :class="{ 'is-active': showNav }">

            <div class="navbar-end">
            <router-link class="navbar-item is-tab" :key="nav.li" v-for="nav in navs" :to="nav.url" :class="{'is-active' : nav.isActive}">{{nav.li}}</router-link>
            </div>
        </div>
    </div>
    </nav>
</section>
</template>
<script>
export default {
  name: 'Nav',
  data () {
    return {
        showNav: false,
        navs:[{
            li: this.$root.$t('m.home'),
            url: '/',
            isActive:true
        },
        {
            li: this.$root.$t('m.blog'),
            url: '/blog'
        },
        {
            li: this.$root.$t('m.photos'),
            url: '/gallery'
        },
        {
            li: this.$root.$t('m.about'),
            url: '/about'
        }],
        activeMenuIndex:0
    }
  },
  methods: {
      getMenuList(){
          var page=this.$route.path;
          var ismatched=false;
          for(let i=0,len=this.navs.length;i<len;i++){                  
                this.navs[i].isActive=false;
                if(page==this.navs[i].url){
                    this.navs[i].isActive=true;
                    ismatched=true;
                }
          }
          if(ismatched){
              this.$forceUpdate();
          }
      },
      init(){
          this.activeMenuIndex=store.state.menuIndex;
      }
  },
    mounted(){
        this.getMenuList();
    }
}
</script>




